// GENERAL 
body {
    font-family: $mainFont, sans-serif;
    font-size: $mainSize;
    font-weight: 400;
    line-height: 1.9;
    color: $darkColor;
    background-color: #fffefe;
    overflow: hidden;
    position: relative;


    &:after {
        content: "";
        background-image: url("../img/bg2.png");
        background-size: cover;
        position: absolute;
        bottom: 0;
        height: 900px;
        width: 100%;
        z-index: 0;
    }
}

.section {
    padding: 2rem 1.5rem;
}

.section-title {
    margin-bottom: 4rem;


    h2 {
        font-family: $titleFont, sans-serif;
        text-transform: capitalize;
        position: relative;
        color: $darkColor;
        margin-bottom: 2rem;
        z-index: 2;
        position: relative;

        &:before {
            content: '';
            height: 55px;
            width: 55px;
            background-color: $mainColor;
            border-radius: 15px;
            position: absolute;
            top: -25%;
            transform: rotate(-45deg);
            z-index: -1;
        }

        &:after {
            content: '';
            height: 10px;
            width: 100px;
            background-color: transparent;
            border-style: solid;
            border-color: $mainColor transparent;
            border-width: 2px;
            position: absolute;
            bottom: 1px;
            right: auto;
            transform: skewX(-50deg);
        }
    }


}