// ABOUT
.about {
    position: relative;
    z-index: 3;

    .content {
        position: relative;

        .picture {
            max-width: 262px;
            margin: auto;
        }

        .button {
            margin-bottom: 25px;
        }

        p {
            font-size: 1.25rem;
        }

        .signature {
            width: 120px;
            height: auto;
            margin: auto;
        }

        &:before {
            content: '';
            position: absolute;
            top: 20px;
            right: 20px;
            background-image: url('../img/plus-2.png');
            width: 75px;
            height: 73px;
            display: block;
            z-index: 1;
            background-size: cover;
        }

    }

    &:before {
        content: '';
        position: absolute;
        bottom: 20px;
        left: 20px;
        background-image: url('../img/plus-2.png');
        width: 75px;
        height: 73px;
        display: block;
        z-index: 1;
        background-size: cover;
    }
}