// NAVBAR
.navbar {
  margin-top: 0.95rem;

  .navbar-brand {
    a.navbar-item {
      img {
        width: 45px;
        height: auto;
        max-height: none;
      }

      .logo-name {
        font-size: 1rem;
        margin-left: 7px;
        font-weight: 600;
      }
    }
  }

  .navbar-end {
    .navbar-item {
      font-size: 1.2rem;
      text-transform: capitalize;
      color: $greyColor;
    }

    .is-active {
      color: $darkColor;
    }
  }
}

.is-fixed-top {
  margin-top: 0;
  background: #fff !important;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.19), 0 6px 10px rgba(0, 0, 0, 0.23);
}
