form {

    .field {
        margin-bottom: 1rem !important;
    }

    .label {
        font-size: .9rem;
    }

    .control {
        box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);

        input,
        textarea {
            border: 0;
            box-shadow: none;
            border-radius: 0;
        }
    }


}