// PROJECTS
.projects {
    z-index: 1;
    position: sticky;

    h3 {
        color: $darkColor;
    }

    p {
        font-size: 1.25rem;
        margin-bottom: 1rem;
    }

    .project {

        .image {
            max-width: 300px;
            margin: auto;
        }

        .created {
            font-size: 13px;
            color: $mainColor;
        }
    }
}