// HOME
.home {
    position: relative;

    .hero-body:after {

        content: '';
        position: absolute;
        bottom: 132px;
        right: 65px;
        background-image: url('../img/lines-3.png');
        background-size: cover;
        width: 90px;
        height: 100px;

    }

    .content {
        position: relative;


        h1.title {
            font-family: $titleFont;
            color: $darkColor;
            margin-top: 0;
            font-size: 4rem;
        }

        h2.subtitle {
            color: $greyColor;
            font-size: 1.6rem;
            font-weight: 400;
        }

        .image {
            position: relative;
            max-width: 440px;
            height: auto;
            margin: auto;

            &:before {
                content: '';
                position: absolute;
                top: 20px;
                right: -250px;
                background-image: url('../img/plus-2.png');
                width: 75px;
                height: 73px;
                display: block;
                z-index: 1;
                background-size: cover;
            }


        }

        .button {
            border: none;
            background-color: $mainColor;
            color: $darkColor;
            position: relative;

            &:before {
                content: '';
                position: absolute;
                top: -20px;
                right: -55px;
                background-image: url('../img/hand-arrow.png');
                background-size: cover;
                width: 60px;
                height: 60px;
                transform: rotate(50deg);
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: 132px;
            left: -65px;
            background-image: url('../img/lines-4.png');
            background-size: cover;
            width: 90px;
            height: 100px;
        }

        &:after {
            content: '';
            position: relative;
            top: 0;
            right: 0;
            background-image: url('../img/plus-2.png');
            width: 120px;
            height: 100px;
            display: block;
            z-index: 1;
        }
    }

    &:before {
        content: '';
        height: 1000px;
        width: 1000px;
        border-radius: 120px;
        position: absolute;
        top: -450px;
        left: 50px;
        transform: rotateZ(-45deg);
        background-image: linear-gradient(to right, $mainColor, $mainColorAlt);
        background-color: $mainColor;
    }
}