// SKILLS
.skills {
    position: relative;
    z-index: 1;

    .column {
        padding: 0 0.75rem;
    }

    .skill {
        position: relative;

        .progress {
            font-size: calc(1.25rem / 1.5);
            line-height: 1.25rem;
        }

        .skill-name {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            font-size: calc(1rem / 1.5);
            line-height: 1rem;
            font-size: calc(1.25rem / 1.5);
            line-height: 1.25rem;
            font-weight: 600;
            color: $greyColor;
            text-transform: uppercase;
        }
    }

    &:before {
        content: '';
        height: 650px;
        width: 600px;
        background-color: #fafafa;
        border-radius: 120px;
        position: absolute;
        top: -150px;
        right: -300px;
        transform: rotateZ(-45deg);
    }
}